import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, MenuController, App } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { Contacts, Contact, ContactField, ContactName } from '@ionic-native/contacts';
import { CallNumber } from '@ionic-native/call-number';
import { EmailComposer } from '@ionic-native/email-composer';
import { ProtectedPage } from '../protected-page/protected-page';
import { UsersService } from '../../providers/users-service';

import *  as AppConfig from '../../app/config';
import * as $ from 'jquery';

@IonicPage()
@Component({
    selector: 'page-agenda',
    templateUrl: 'agenda.html',
})
export class AgendaPage extends ProtectedPage {
    private cfg: any;
    socios: any = [];
    sociosAgrupados: any = [];
    proveedores: any = [];
    proveedoresAgrupados: any = [];
    constructor(
        public navCtrl: NavController,
        public navParams: NavParams,
        public menuCtrl: MenuController,
        public storage: Storage,
        public usersService: UsersService,
        public contacts: Contacts,
        public callNumber: CallNumber,
        public emailComposer: EmailComposer,
        public appCtrl: App) {
        super(navCtrl, navParams, storage, appCtrl);
        this.cfg = AppConfig.cfg;
    }

    ionViewDidLoad() {
        this.usersService.getAll('socios').then(socios => {
            this.socios = socios;
            console.log(this.socios);
            this.groupSocios(this.socios, this.sociosAgrupados);
        });
        this.usersService.getAll('proveedores').then(proveedores => {
            this.proveedores = proveedores;
        });
    }
    openPage(page: string, user) {
        this.navCtrl.push(page, {
            user: user
        });
    }
    groupSocios(users, output) {
        let sortedContacts = users.sort();
        let currentLetter = false;
        let currentContacts = [];
        sortedContacts.forEach((value, index) => {
            if (value.doc.name.charAt(0) != currentLetter) {
                currentLetter = value.doc.name.charAt(0);
                let newGroup = {
                    letter: currentLetter,
                    users: []
                };
                currentContacts = newGroup.users;
                output.push(newGroup);
            }
            currentContacts.push(value.doc);
        });
    }
    saveContact(user: any) {
        if (user.phonenum) {
            if ((<any>window).cordova) {
                let contact: Contact = this.contacts.create();
                let nombreUser = user.name.split(" ");
                if(contact){
                    contact.name = new ContactName(null, nombreUser[1], nombreUser[0]);
                    contact.phoneNumbers = [new ContactField('mobile', user.phonenum)];
                    contact.emails = [new ContactField('email', user.email)];
                    //contact.photos = [new ContactField('photo', user._attachments)];
                    contact.addresses = [new ContactField('address', user.direction)];
                    contact.organizations = [new ContactField('organization', user.name_ferreteria)];
                    contact.save().then(() =>
                        console.log('Contact saved!', contact),
                        (error: any) => console.error('Error saving contact.', error));
                }
            }
        }
    }
    callToContact(user: any) {
        if (user.phonenum) {
            this.callNumber.callNumber(user.phonenum, true).then(() => console.log('Launched dialer!')).catch(() => console.log('Error launching dialer'));
        }
    }
    mailToContact(user: any) {
        console.log(user);
        if (user.email) {
            console.log("Envio Correo", user.email);
        }
    }
}



